<template>
  <div id="feedback-page">
    <t-page title="Обратная связь">
      <template v-if="!success">
        <t-form
            class="feedback-form"
            :fields="fields"
            :is-loading="isLoading"
            @submit="handleSubmit"
        />
      </template>
      <template v-else>
        <TSuccessCard
            title="Отзыв отправлен"
            description="Мы обязательно с вами свяжемся"
        />
      </template>
    </t-page>
  </div>
</template>

<script>
import TForm from "../components/TForm";
import TPage from "../components/TPage";
import {getValidators, VALIDATORS_NAMES} from "@/ext/validators";
import {handleRequest} from "@/ext";
import TSuccessCard from "../components/TSuccessCard";

export default {
  name: 'Feedback',
  data: () => ({
    success: false,
    isLoading: false,
    fields: [
      {
        id: 1,
        component: 'v-text-field',
        name: 'email',
        props: {
          label: 'E-mail',
          placeholder: 'Введите e-mail',
          required: true,
          rules: getValidators(VALIDATORS_NAMES.EMAIL),
        },
      },
      {
        id: 2,
        component: 'v-textarea',
        name: 'feedback',
        props: {
          label: 'Отзыв',
          placeholder: 'Ваш отзыв',
          autoGrow: true,
          noResize: true,
          required: true,
          rules: getValidators(VALIDATORS_NAMES.FEEDBACK),
        },
      },
    ],
  }),
  methods: {
    async handleSubmit() {
      this.isLoading = true

      const response = await handleRequest()

      this.isLoading = false
      this.success = true
    }
  },
  components: {
    TForm,
    TPage,
    TSuccessCard
  },
}
</script>

<style lang="scss">
.feedback-form {
  max-width: 500px;
  width: 100%;
}
</style>
