const arrFromStr = str => str.split('')

const randomInt = maxInt => Math.floor(Math.random() * maxInt)

const randomArrayValue = arr => arr[randomInt(arr.length)]

const LocalStorageBridge = (key, initValue) => ({
    key,
    set(options) {
        const stringifyOptions = JSON.stringify(options)

        localStorage.setItem(this.key, stringifyOptions)
    },
    get() {
        let stringifyOptions = localStorage.getItem(this.key)

        if (!stringifyOptions) {
            this.set(initValue)

            return initValue
        }

        return JSON.parse(stringifyOptions)
    },
})

export {
    arrFromStr,
    randomInt,
    randomArrayValue,
    LocalStorageBridge,
}