import {arrFromStr, LocalStorageBridge, randomArrayValue} from "@/generator/helpers";

const LS_OPTIONS_KEY = 'generatorOptions'
const LS_OPTIONS_INIT_VALUE = {
    length: 14,
    enabledSymbols: ['letters', 'specials', 'numbers'],
}

const LS_OPTIONS = LocalStorageBridge(
    LS_OPTIONS_KEY,
    LS_OPTIONS_INIT_VALUE
)

const SYMBOLS = {
    letters: arrFromStr('abcdefghijklmnopqrstuvwxyz'),
    specials: arrFromStr('!@#$%^:&?*(){}[]_-+='),
    numbers: arrFromStr('0123456789'),
}

const getAvailableSymbolsTypes = enabledSymbols => {
    const availableSymbolsTypes = []

    for (const enabledSymbol of enabledSymbols) {
        if (enabledSymbol in SYMBOLS) availableSymbolsTypes.push(enabledSymbol)
    }

    return availableSymbolsTypes
}

const generatePassword = ({ length, enabledSymbols }) => {
    let password = ''

    enabledSymbols = getAvailableSymbolsTypes(enabledSymbols)

    for (let i = 0; i < length; i++) {
        const curSymbolType = randomArrayValue(enabledSymbols)
        const curSymbol = randomArrayValue(SYMBOLS[curSymbolType])

        password += curSymbol
    }

    return password
}

export {
    generatePassword,
    LS_OPTIONS
}