import { LocalStorageBridge } from "@/generator/helpers";

const LS_PASSWORDS_KEY = 'savedPasswords'
const LS_PASSWORDS_INIT_VALUE = []

const LS_PASSWORDS = LocalStorageBridge(
    LS_PASSWORDS_KEY,
    LS_PASSWORDS_INIT_VALUE
)

LS_PASSWORDS.getFreeId = function () {
    const passwords = this.get()
    let maxPasswordIdx

    for (const password of passwords) {
        if (!maxPasswordIdx || password.id > maxPasswordIdx) {
            maxPasswordIdx = password.id
        }
    }

    maxPasswordIdx = maxPasswordIdx ?? 0

    return maxPasswordIdx + 1
}

LS_PASSWORDS.add = function (password) {
    const passwords = this.get()

    console.log(passwords)

    console.log(password)

    passwords.push({
        ...password,
        id: this.getFreeId(),
        date: Date.now(),
    })

    this.set(passwords)
}

LS_PASSWORDS.delete = function (passwordId) {
    const passwords = this.get()
    const searchPassIdx = passwords.findIndex(pass => pass.id === passwordId)

    if (searchPassIdx !== -1) {
        passwords.splice(searchPassIdx, 1)

        this.set(passwords)
    }
}

export {
    LS_PASSWORDS
}