<template>
  <v-card class="t-popup-save">
    <t-form
      ref="tForm"
      :key="key"
      :fields="fields"
      :is-loading="isLoading"
      btn-text="Сохранить"
      @submit="handleSubmit"
    />
  </v-card>
</template>

<script>
import TForm from "@/components/TForm";
import {getValidators, VALIDATORS_NAMES} from "@/ext/validators";
import {handleRequest} from "@/ext";
import {LS_PASSWORDS} from "@/generator";

export default {
  name: "TPopupOptions",
  data: () => ({
    isLoading: false,
    key: 0,
  }),
  props: {
    password: {
      type: String,
      required: true,
    },
    closePopup: {
      type: Function,
      required: true,
    },
    changePersistent: {
      type: Function,
      required: true,
    },
    showSuccess: {
      type: Function,
      required: true,
    },
    isOpen: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    isOpen() {
      this.$refs.tForm.reset()
    },
  },
  computed: {
    fields() {
      return [
        {
          id: 1,
          component: 'v-text-field',
          name: 'passwordName',
          props: {
            label: 'Название пароля',
            placeholder: 'Введите название пароля',
            rules: getValidators(VALIDATORS_NAMES.PASSWORD_NAME),
          },
        },
        {
          id: 0,
          component: 'v-text-field',
          name: 'password',
          initValue: this.password,
          props: {
            label: 'Пароль',
            placeholder: 'Введите имя',
            required: true,
            disabled: true,
          },
        },
        {
          id: 2,
          component: 'v-checkbox',
          name: 'agreement',
          props: {
            label: 'Согласен с правилами сайта',
            required: true,
            rules: getValidators(VALIDATORS_NAMES.AGREEMENT),
          },
        },
      ]
    },
  },
  components: {
    TForm
  },
  methods: {
    beforeSubmit() {
      this.changePersistent(true)
      this.isLoading = true
    },
    afterSubmit() {
      this.changePersistent(false)
      this.isLoading = false
      this.showSuccess()
    },
    handleSubmit(password) {
      console.log(password)


      this.beforeSubmit()

      LS_PASSWORDS.add(password)

      this.afterSubmit()
      this.closePopup()
    },
  },
}
</script>

<style scoped>
.t-popup-save {
  padding: 40px 20px;
}
</style>