<template>
  <v-dialog
      max-width="500"
      v-model="showDialog"
      :persistent="persistent"
  >
    <slot v-bind="{ changePersistent, closeModal }" />
  </v-dialog>
</template>

<script>
import TForm from "@/components/TForm";

export default {
  name: "TPopup",
  data: () => ({
    showDialog: false,
    persistent: false,
  }),
  props: {
    value: false,
  },
  watch: {
    value() {
      this.showDialog = this.value
    },
    showDialog() {
      this.$emit('input', this.showDialog)
    },
  },
  components: {
    TForm,
  },
  methods: {
    changePersistent(val) {
      this.persistent = val
    },
    closeModal() {
      this.showDialog = false
    },
  },
}
</script>

<style scoped>
.t-popup {
  width: 100%;
  max-width: 500px;
}
</style>