<template>
  <div id="feedback-page">
    <t-page title="Сохранённые пароли">
      <div class="saved-passwords">
        <template v-if="passwords.length === 0">
          <div class="saved-passwords-empty">Нет сохранённых паролей</div>
        </template>
        <div class="saved-passwords-item" v-for="(pass, idx) in passwords">
          <div class="saved-passwords-item-site">{{ pass.passwordName }}</div>
          <div class="saved-passwords-item-pass">{{ getFormattedPassword(pass) }}</div>
          <div class="saved-passwords-item-actions">
            <v-btn @click="switchPasswordVisible(idx)" icon>
              <v-icon v-show="pass.visibility">mdi-eye-off</v-icon>
              <v-icon v-show="!pass.visibility">mdi-eye</v-icon>
            </v-btn>
            <v-btn @click="deletePassword(pass.id)" icon>
              <v-icon>mdi-trash-can</v-icon>
            </v-btn>
          </div>
        </div>
      </div>
    </t-page>
  </div>
</template>

<script>
import TPage from "@/components/TPage";
import {LS_PASSWORDS} from "@/generator";

export default {
  name: 'Saved',
  data: () => ({
    passwords: [],
  }),
  created() {
    this.updatePasswords()
  },
  methods: {
    switchPasswordVisible(idx) {
      const newVisibility = !this.passwords[idx].visibility

      this.$set(this.passwords[idx], 'visibility', newVisibility)
    },
    updatePasswords() {
      this.passwords = LS_PASSWORDS.get()
    },
    deletePassword(id) {
      LS_PASSWORDS.delete(id)
      this.updatePasswords()
    },
    getFormattedPassword(pass) {
      const { visibility, password } = pass

      return visibility ? password : password.split('').map(char => '•').join('')
    },
  },
  components: {
    TPage
  },
}
</script>

