const VALIDATORS_RULES = {
    required: v => !!v
        || 'Обязательное поле',
    hasNotNumbers: v => !/[0-9]/.test(v)
        || 'Поле не должно содержать цифр',
    hasNotSpecChars: v => !/[~`!@#$%^&*+=\-\[\]\\';,/{}|":<>?]/.test(v)
        || 'Поле не должно содержать спец. символов',
    validEmail: v => /.+@.+\..+/.test(v)
        || 'Введите валидный email',
    enWord: v => /^[a-zA-Z]+$/.test(v)
        || 'Поле должно содержать только английские буквы',
    ruWord: v => /^[а-яА-Я]+$/.test(v)
        || 'Поле должно содержать только русские буквы',
}

const VALIDATORS = {
    name: [
        VALIDATORS_RULES.required,
        VALIDATORS_RULES.hasNotSpecChars,
        VALIDATORS_RULES.hasNotNumbers,
    ],
    email: [
        VALIDATORS_RULES.required,
        VALIDATORS_RULES.validEmail
    ],
    feedback: [
        VALIDATORS_RULES.required
    ],
    enWord: [
        VALIDATORS_RULES.required,
        VALIDATORS_RULES.enWord
    ],
    ruWord: [
        VALIDATORS_RULES.required,
        VALIDATORS_RULES.ruWord
    ],
    passwordName: [
        VALIDATORS_RULES.required
    ],
    agreement: [
        VALIDATORS_RULES.required
    ],
}

export const VALIDATORS_NAMES = Object.freeze({
    EMAIL: 'email',
    NAME: 'name',
    FEEDBACK: 'feedback',
    EN_WORD: 'enWord',
    RU_WORD: 'ruWord',
    PASSWORD_NAME: 'passwordName',
    AGREEMENT: 'agreement',
})

export const getValidators = validatorName => [
    ...VALIDATORS[validatorName]
]