import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Saved from "@/views/Saved";
import FreqQuestions from "@/views/FreqQuestions";
import Feedback from "@/views/Feedback";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/feedback',
    name: 'Saved',
    component: Saved,
  },
  {
    path: '/q-and-a',
    name: 'FreqQuestions',
    component: FreqQuestions,
  },
  {
    path: '/feedback',
    name: 'Feedback',
    component: Feedback,
  },
]

const router = new VueRouter({
  routes
})

export default router
