<template>
  <v-form
    class="t-form-wrapper"
    v-model="isValid"
    :disabled="isLoading"
    ref="form"
    @submit.prevent="handleSubmit"
  >
    <v-select v-if="false"/>
    <v-checkbox v-if="false"/>
    <v-text-field v-if="false"/>
    <v-textarea v-if="false"/>
    <v-file-input v-if="false"/>
    <component
      v-for="field in fields"
      :key="field.key"
      v-bind="{
        ...baseFieldsProps,
        ...field.props,
        name: field.name,
        is: field.component,
        value: fieldValues[field.name],
      }"
      @input="val => handleFieldInput(val, field.name)"
    />
    <slot name="button">
      <v-btn
        class="mt-4"
        type="submit"
        color="primary"
        block
        large
        :loading="isLoading"
      >{{ btnText }}</v-btn>
    </slot>
  </v-form>
</template>

<script>
export default {
  name: "TForm",
  data: () => ({
    isValid: false,
    fieldValues: {},
  }),
  props: {
    fields: {
      type: Array,
      required: true,
    },
    btnText: {
      type: String,
      default: 'Отправить',
    },
    baseFieldsProps: {
      type: Object,
      default: () => ({
        filled: true,
        dense: true
      }),
    },
    isLoading: {
      type: Boolean,
      default: false,
    },
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      for (const field of this.fields) {
        if (field.initValue) {
          this.handleFieldInput(field.initValue, field.name)
        }
      }
    },
    validateForm() {
      this.$refs.form.validate()
    },
    handleSubmit(event) {
      this.validateForm()

      if (!this.isValid) return

      this.$emit('submit', this.fieldValues)
    },
    reset() {
      this.$refs.form.reset()
      this.$nextTick().then(this.init)
    },
    handleFieldInput(val, fieldName) {
      this.$set(this.fieldValues, fieldName, val)
    },
  },
}
</script>

<style>
.t-form-wrapper {
  width: 100%;
}
</style>