<template>
  <t-page title="Генератор паролей">
    <div class="page-home-generator">
      <v-card class="page-home-generator-card" elevation="0" color="transparent">
        <div
          class="page-home-generator-card-value"
          :class="{
            'page-home-generator-card-value__disable': !password,
          }"
          ref="password"
        >{{ password }}</div>
        <div class="page-home-generator-card-actions">
          <v-btn
            v-for="btn in actionButtons"
            :key="btn.id"
            @click="btn.click"
            height="60"
            dark
          >
            <v-icon>{{ btn.icon }}</v-icon>
          </v-btn>
        </div>
      </v-card>
      <t-popup v-model="saveModal">
        <template v-slot="{ changePersistent, closeModal }">
          <t-popup-save
            :is-open="saveModal"
            :change-persistent="changePersistent"
            :close-popup="closeModal"
            :password="password"
            :show-success="openSaveSnackbar"
          />
        </template>
      </t-popup>
      <t-popup v-model="optionsModal"/>
      <v-snackbar
        v-bind="{
          ...baseSnackbarProps
        }"
        v-model="copySnackbar"
      >
        <v-icon class="mr-2">mdi-content-copy</v-icon>Пароль скопирован
        <template v-slot:action="{ attrs }">
          <v-btn
            icon
            v-bind="attrs"
            @click="copySnackbar = false"
          >
            <v-icon>mdi-close-circle</v-icon>
          </v-btn>
        </template>
      </v-snackbar>
      <v-snackbar
        v-bind="{
          ...baseSnackbarProps
        }"
        v-model="saveSnackbar"
      >
        <v-icon class="mr-2">mdi-bookmark-plus</v-icon>Пароль сохранён
        <template v-slot:action="{ attrs }">
          <v-btn
            icon
            v-bind="attrs"
            @click="saveSnackbar = false"
          >
            <v-icon>mdi-close-circle</v-icon>
          </v-btn>
        </template>
      </v-snackbar>
    </div>
  </t-page>
</template>

<script>
import { LS_OPTIONS, generatePassword } from "@/generator";
import TPopupSave from "@/components/TPopupSave";
import TPopup from "@/components/TPopup";
import TPage from "@/components/TPage";
import TPopupOptions from "@/components/TPopupOptions";

export default {
  name: 'Home',
  data: () => ({
    saveModal: false,
    optionsModal: false,
    copySnackbar: false,
    saveSnackbar: false,
    password: '',
    options: LS_OPTIONS.get(),
  }),
  watch: {
    options() {
      LS_OPTIONS.set(this.options)
    },
  },
  computed: {
    baseSnackbarProps() {
      return {
        minWidth: '200',
        maxWidth: '400',
        width: '100vw',
        elevation: '10',
        color: 'primary',
      }
    },
    actionButtons() {
      return [
        {
          id: 0,
          click: this.generateNewPassword,
          icon: 'mdi-restart',
        },
        {
          id: 1,
          click: this.openSaveModal,
          icon: 'mdi-bookmark-plus',
        },
        {
          id: 2,
          click: this.copyPassword,
          icon: 'mdi-content-copy',
        },
        // {
        //   id: 3,
        //   click: this.openOptionsModal,
        //   icon: 'mdi-cog',
        // },
      ]
    },
  },
  components: {
    TPage,
    TPopup,
    TPopupSave,
    TPopupOptions,
  },
  created() {
    this.generateNewPassword()
  },
  methods: {
    generateNewPassword() {
      this.password = generatePassword(this.options)
    },
    openSaveSnackbar() {
      this.saveSnackbar = true
      this.generateNewPassword()
    },
    copyPassword() {
      navigator.clipboard.writeText(this.password)

      this.copySnackbar = true
    },
    openSaveModal() {
      this.saveModal = true
    },
    openOptionsModal() {
      this.optionsModal = true
    },
  },
}
</script>

